.contenedor-producto {
  margin: 5rem;
  border: 1px solid black;
  border-radius: 1rem;
  background-color: white;
  display: flex;
  flex-direction: column;
}
.contenedor-producto img {
  border-radius: 1rem;
  border: none;
  padding: 3rem;
}
.contenedor-producto .imagen-producto {
  width: 100%;
  display: flex;
  justify-content: center;
}
.contenedor-producto .imagen-producto img {
  width: 100%;
  max-width: 60rem;
}
.contenedor-producto .informacion-producto {
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: start;
}
.contenedor-producto .informacion-producto p {
  font-size: 1.8rem;
  margin: 0;
  text-justify: 1.5;
  margin-bottom: 1rem;
}
.contenedor-producto .informacion-producto span {
  font-size: 2.2rem;
  font-weight: 700;
  display: block;
}
.contenedor-producto .informacion-producto h2 {
  text-align: start;
  margin: 0;
  margin-bottom: 5rem;
}

.formulario-producto {
  margin-top: 3rem;
}
.formulario-producto .formulario-compra {
  display: flex;
  flex-direction: column;
}
.formulario-producto .input-number {
  width: 12rem;
  font-size: 1.5rem;
  height: 3rem;
  margin-bottom: 1rem;
}
.formulario-producto label {
  font-size: 1.8rem;
}

.boton-comprar {
  background-color: #11234d;
  color: white;
  font-weight: 600;
  transition: all linear 0.2s;
}

.boton-carrito {
  background-color: #F2AC00;
  font-weight: 600;
  transition: all linear 0.2s;
}

.boton-carrito:hover {
  background-color: #bf8800;
}

.boton-comprar:hover {
  background-color: #1a3677;
}

@media (min-width: 1024px) {
  .contenedor-producto {
    flex-direction: row;
  }
  .contenedor-producto img {
    padding: none;
  }
  .imagen-producto {
    width: 40%;
    border-right: 1px solid black;
  }
  .informacion-producto {
    width: 60%;
  }
}